export default class Popup {
  constructor(popupSelector) {
    this._popupSelector = popupSelector;
    this._popup = document.querySelector(this._popupSelector);
    this._popupButtonClose = this._popup.querySelector(".popup__button-close");
  }

  // Ð¿ÑÐ¸Ð²Ð°ÑÐ½ÑÐ¹ Ð¼ÐµÑÐ¾Ð´ Ð·Ð°ÐºÑÑÑÐ¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð° ÐºÐ»Ð°Ð²Ð¸ÑÐµÐ¹ Esc
  // Ð¿ÑÐ¸ Ð¸ÑÐ¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°Ð½Ð¸Ð¸ ÑÑÑÐµÐ»Ð¾ÑÐ½Ð¾Ð¹ ÑÑÐ½ÐºÑÐ¸Ð¸ bind Ð½Ðµ Ð½ÑÐ¶ÐµÐ½
  _handleEscClose = (evt) => {
    if (evt.key === "Escape") {
      this.close();
    }
  };

  // Ð¿ÑÐ±Ð»Ð¸ÑÐ½ÑÐ¹ Ð¼ÐµÑÐ¾Ð´ Ð¾ÑÐºÑÑÑÐ¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð°
  open() {
    document.addEventListener("keydown", this._handleEscClose);
    this._popup.classList.add("popup_opened");
  }

  // Ð¿ÑÐ±Ð»Ð¸ÑÐ½ÑÐ¹ Ð¼ÐµÑÐ¾Ð´ Ð·Ð°ÐºÑÑÑÐ¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð°
  close() {
    document.removeEventListener("keydown", this._handleEscClose);
    this._popup.classList.remove("popup_opened");
  }

  /* Ð¿ÑÐ±Ð»Ð¸ÑÐ½ÑÐ¹ Ð¼ÐµÑÐ¾Ð´, ÐºÐ¾ÑÐ¾ÑÑÐ¹ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÑ ÑÐ»ÑÑÐ°ÑÐµÐ»Ñ ÐºÐ»Ð¸ÐºÐ° Ð¸ÐºÐ¾Ð½ÐºÐµ Ð·Ð°ÐºÑÑÑÐ¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð°.
  ÐÐ¾Ð´Ð°Ð»ÑÐ½Ð¾Ðµ Ð¾ÐºÐ½Ð¾ ÑÐ°ÐºÐ¶Ðµ Ð·Ð°ÐºÑÑÐ²Ð°ÐµÑÑÑ Ð¿ÑÐ¸ ÐºÐ»Ð¸ÐºÐµ Ð½Ð° Ð·Ð°ÑÐµÐ¼Ð½ÑÐ½Ð½ÑÑ Ð¾Ð±Ð»Ð°ÑÑÑ Ð²Ð¾ÐºÑÑÐ³ ÑÐ¾ÑÐ¼Ñ */
  setEventListeners() {
    // Ð·Ð°ÐºÑÑÑÐ¸Ðµ Ð¿Ð¾Ð¿Ð°Ð¿Ð° Ð¿Ð¾ ÐºÐ½Ð¾Ð¿ÐºÐµ Ñ
    this._popupButtonClose.addEventListener("click", () => {
      this.close();
    });

    this._popup.addEventListener("mousedown", (evt) => {
      if (evt.target == evt.currentTarget) {
        this.close();
      }
    });
  }
}
