/** Ð¿ÐµÑÐµÐ¼ÐµÐ½Ð½Ð°Ñ ÑÐ¾Ð·Ð´Ð°Ð½Ð° Ð´Ð»Ñ ÑÐ²ÑÐ·ÑÐ²Ð°Ð½Ð¸Ñ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑÐ° ÐºÐ»Ð°ÑÑÐ° Api Ñ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑÐ¾Ð¼ ÐºÐ»Ð°ÑÑÐ° Card -
 *  Ð´Ð»Ñ Ð¿ÑÐ¸ÑÐ²Ð¾ÐµÐ½Ð¸Ñ ÑÐ²Ð¾ÐµÐ³Ð¾ id Ð¸ ÐµÐ³Ð¾ Ð¸ÑÐ¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°Ð½Ð¸Ñ Ð¿ÑÐ¸ Ð¾ÑÐ¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ð¸ Ð½Ð° ÐºÐ°ÑÑÐ¾ÑÐºÐµ Ð¸ÐºÐ¾Ð½ÐºÐ¸ ÐºÐ¾ÑÐ·Ð¸Ð½Ñ*/
let ownerId = null;

/** Ð¿ÐµÑÐµÐ¼ÐµÐ½Ð½Ð°Ñ ÑÐ¾Ð·Ð´Ð°Ð½Ð° Ð´Ð»Ñ ÑÐ²ÑÐ·ÑÐ²Ð°Ð½Ð¸Ñ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑÐ° ÐºÐ»Ð°ÑÑÐ° Card Ñ
 * ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑÐ¾Ð¼ ÐºÐ»Ð°ÑÑÐ° PopupWithConfirmation - Ð´Ð»Ñ ÑÐ´Ð°Ð»ÐµÐ½Ð¸Ñ ÐºÐ°ÑÑÐ¾ÑÐºÐ¸ Ñ ÑÐ°Ð¹ÑÐ° */
